.container {
    display: none;
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0px;
    left: 0px;
    z-index: 1000;
    @media screen and ( max-width: 1170px ) {
        display: flex; }
    .navMobile {
        background: white;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .item {
            padding: 10px;
            color: black;
            a {
                position: relative;
                text-decoration: none;
                color: black;
                font-size: 20px; }
            a.active {
                &::after {
                    content: '';
                    width: 100%;
                    position: absolute;
                    height: 1px;
                    background: rgba(0,0,0,0.5);
                    left: 0;
                    bottom: 0; } } } } }
.burger {
    display: none;
    @media screen and ( max-width: 1170px ) {
        display: flex; }
    position: fixed;
    top: 30px;
    left: 20px;
    img {
        width: 32px;
        height: 32px; } }


